import React, { Component } from 'react';

// -- json data
import CalloutImageJson from '../../static/json/map_callouts.json';

class callout_image_box extends Component {
	render() {
		return (
			<div>
				<h2>test</h2>
				{CalloutImageJson.map((item, index) => {
					if (item.map_name === this.props.activeMap) {
						item.images.map(image => {
							setTimeout(() => {
								console.log(image.image_path);
							}, 1000);
						});
					}
				})}
			</div>
		);
	}
}

export default callout_image_box;
